import React from "react";
import logo from "./init-logo.jpg";

export interface LoaderProps extends React.HTMLProps<any> {
    imgSize?: "S" | "M" | "L"
}

export const Loader: React.FC<LoaderProps> = (props: LoaderProps) => {

    let width = "80%";
    switch (props.imgSize) {
        case "M":
            width = "50%";
            break;
        case "S":
            width = "25%";
            break;
    }

    return (
        <div style={{"height": "100%", "background": "#fff"}}
             className={"display-flex justify-content-center align-content-center align-items-center"}>
            <img className={"animated-blink"} src={logo} width={width}/>
        </div>
    )
}
