import {addDoc, getDoc, getDocs, collection, doc, query, where} from "firebase/firestore";
import {BatchOfGoods} from "./BatchOfGoods";
import {Manufacturer} from "./Manufacturer";
import {Product} from "./Product";
import {ApiClientInterface} from "./ApiClientInterface";
import {Firestore, GeoPoint, Timestamp} from "@firebase/firestore";
import {QRScanInfo} from "../components/QRScanner";


export class FirebaseClient implements ApiClientInterface {

    protected _firestore: Firestore

    constructor(firestore: Firestore) {
        this._firestore = firestore;
    }

    async getBatchOfProduct(batchOfProductId: string): Promise<BatchOfGoods> {
        const ref = doc(this._firestore, "batch", batchOfProductId);
        const res = await getDoc(ref);
        if (res.exists()) {
            const data = res.data();

            let manufacturer: Manufacturer|undefined;
            const manId = data.manufacture;
            if (manId) {
                manufacturer = await this.getManufacturer(manId);
            }

            let product: Product|undefined;
            const productId = data.product;
            if (productId) {
                product = await this.getProduct(productId);
            }

            return {
                id: res.id,
                count: data.count,
                createdDate: (data.createdAt as Timestamp)?.toDate(),
                manufacturer: manufacturer,
                product: product,
            }


        }
        throw new Error("not found");
    }

    async getManufacturer(manId: string): Promise<Manufacturer> {
        const ref = doc(this._firestore, "manufacture", manId);
        const res = await getDoc(ref);
        if (res.exists()) {
            const data = res.data();
            return {
                name: data.name,
                address: data.addrees,
                batchesOfProducts: [],
            }
        }
        throw new Error("not found");
    }

    async getProduct(productId: string): Promise<Product> {
        const ref = doc(this._firestore, "product", productId);
        const res = await getDoc(ref);
        if (res.exists()) {
            const data = res.data();
            return {
                name: data.name || "",
                description: data.description || "",
                batchesOfProducts: [],
                category: data.category,
                pictureUrl: data.picture,
            }
        }
        throw new Error("not found");
    }

    async logScan(scanInfo: QRScanInfo): Promise<any> {

        let point: any;
        if (scanInfo.location?.coords?.latitude && scanInfo.location?.coords?.longitude) {
            point = new GeoPoint(scanInfo.location?.coords?.latitude, scanInfo.location.coords.longitude);
        }

        return addDoc(collection(this._firestore, "log"), {
            timestamp: Timestamp.fromDate(scanInfo.scannedAt),
            location: point,
            ref: scanInfo.batchOfProductId,
        });


    }
}
