import React, {useEffect} from "react";
import {Page} from "framework7-react";
import {Loader} from "../components/Loader";

export interface InitProps {
    onInit(): void
}

export const Init: React.FC<InitProps> = (props: InitProps) => {

    console.log("Init screen", props);

    useEffect(() => {
        console.log("SCHEDULE TIMT");
        const id = setTimeout(() => {
            console.log("WORK TIMT");
            props.onInit();
        }, 3000);

        return () => {
            console.log("CLEAR TIMT");
            clearTimeout(id);
        }
    }, [])

    return (
        <Page>
            <Loader/>
        </Page>
    )
}
