import Airtable, {Records} from "airtable";
import Record from "airtable/lib/record";
import {BatchOfGoods} from "./BatchOfGoods";
import {Manufacturer} from "./Manufacturer";
import {Product} from "./Product";
import {ApiClientInterface} from "./ApiClientInterface";
import {Log} from "./Log";
import {QRScanInfo} from "../components/QRScanner";


const DATABASE_ID = "appSWfk9bUFp1X9i4";
const BATCH_OF_GOODS_TABLE = "tblxSczIEnMERTtjJ";
const MANUFACTURER_TABLE = "tblms57Aoti6C8TJs";
const PRODUCT_TABLE = "tblTqmgc6aekIw1DU";
const LOG_TABLE = "tblzCk2BEZnf5KhKj";

export class AirtableClient implements ApiClientInterface{

    protected _airtable: Airtable

    constructor(airtable: Airtable) {
        this._airtable = airtable;
    }

    async getBatchOfProduct(batchOfProduct: string): Promise<BatchOfGoods> {
        const res = await this._airtable
            .base(DATABASE_ID)
            .table(BATCH_OF_GOODS_TABLE)
            .find(batchOfProduct);

        let manufacturer: Manufacturer|undefined;
        const manId = res.fields["Manufacturer"] && (res.fields["Manufacturer"] as ReadonlyArray<string>)[0];
        if (manId) {
            manufacturer = await this.getManufacturer(manId);
        }

        let product: Product|undefined;
        const productId = res.fields["Product"] && (res.fields["Product"] as ReadonlyArray<string>)[0];
        if (productId) {
            product = await this.getProduct(productId);
        }

        return {
            id: res.id,
            count: res.fields["Count"] as number,
            createdDate: new Date(res.fields["Created Date"] as string),
            manufacturer: manufacturer,
            product: product,
        }
    }

    async getManufacturer(manId: string): Promise<Manufacturer> {
        const res = await this._airtable
            .base(DATABASE_ID)
            .table(MANUFACTURER_TABLE)
            .find(manId).then<Record<any>>((res: Record<any>) => {
                console.log(res);

                return res;
            });

        return {
            name: res.fields["Name"],
            address: res.fields["Address"],
            batchesOfProducts: res.fields["BatchesOfProducts"],
        }
    }

    async getProduct(productId: string): Promise<Product> {
        const res = await this._airtable
            .base(DATABASE_ID)
            .table(PRODUCT_TABLE)
            .find(productId).then<Record<any>>((res: Record<any>) => {
                console.log(res);

                return res;
            });

        return {
            name: res.fields["Name"],
            description: res.fields["Description"],
            batchesOfProducts: res.fields["BatchesOfProducts"],
            category: res.fields["Category"],
            pictureUrl:"",
        }
    }

    async logScan(scanInfo: QRScanInfo): Promise<any> {
        const res = await this._airtable
            .base(DATABASE_ID)
            .table(LOG_TABLE)
            .create([
                {
                    fields: {
                        fldOVmSdWvJHkqZa8: [
                            scanInfo.batchOfProductId
                        ]
                    }
                }
            ]);

        return res;
    }
}
