

export class GeoLocation {

    _positionPromise: Promise<GeolocationPosition>|undefined;
    _navigator: Navigator

    constructor(navigator: Navigator) {
        this._navigator = navigator;
        this._refresh();
    }

    async getPosition(refresh: boolean = false): Promise<GeolocationPosition> {
        refresh && this._refresh();
        return this._positionPromise as Promise<GeolocationPosition>;
    }

    protected _refresh() {
        this._positionPromise = new Promise((resolve, reject) => {
            this._navigator.geolocation.getCurrentPosition(
                (pos: GeolocationPosition) => {
                    resolve(pos);
                },
                (err: GeolocationPositionError) => {
                    reject(err);
                });
        });
    }
}
