import React, {useEffect, useState} from "react";
import {Block, Card, CardContent, CardFooter, CardHeader, Col, Icon, Link, Page, Row} from "framework7-react";
import {LinkParams} from "../navigation/LinkParams";
import {QRScanInfo} from "../components/QRScanner";
import {AirtableClient} from "../services/AirtableClient";
import {BatchOfGoods} from "../services/BatchOfGoods";
import {InnerLayout} from "../layouts/InnerLayout";
import {Loader} from "../components/Loader";
import dummyImg from "./dummy-img.png";

export interface ProductProps {
    scanInfo: QRScanInfo

    batchOfProductId: string

    screenTitle: string

    tabbarLinks(): LinkParams[]

    onClickBack(): void

    dataSource: AirtableClient
}

export const Product: React.FC<ProductProps> = (props: ProductProps) => {
    console.log("Product screen", props);

    const [batchOfGoods, setBatchOfGoods] = useState<BatchOfGoods | undefined>();

    useEffect(() => {
        console.log("PROD")
        props.dataSource.getBatchOfProduct(props.batchOfProductId).then((batchOfGoods: BatchOfGoods) => {
            setBatchOfGoods(batchOfGoods);
        });
    }, [props.batchOfProductId, props.dataSource]);

    return (
        <Page>
            <InnerLayout {...props}>
                {!batchOfGoods && <Loader imgSize={"M"}/>}
                <Card>
                    <CardHeader>
                        <img src={batchOfGoods?.product?.pictureUrl || dummyImg} width={"100%"} />
                    </CardHeader>
                    <CardContent>
                        <Row>
                            <Col width={100}>
                                <Icon color={"green"}
                                      ios={"f7:checkmark_seal"}
                                      md={"material:check"}
                                />
                                <span color={"green"}>Verified</span></Col>
                        </Row>
                        {batchOfGoods?.product?.description && (
                            <Row>
                                <Col width={100}>
                                    <Block inset strong>
                                        {batchOfGoods?.product?.description}
                                    </Block>
                                </Col>
                            </Row>
                        )}
                    </CardContent>

                    <CardContent>
                        {batchOfGoods?.product?.category && (
                            <Row>
                                <Col width={50}><b>Category:</b></Col>
                                <Col width={50} className={"flex-justify-content-end"}>
                                    <Link href={"/"}>{batchOfGoods?.product?.category}</Link>
                                </Col>
                            </Row>
                        )}
                        {batchOfGoods?.manufacturer?.name && (
                            <Row>
                                <Col width={50}><b>Manufacturer:</b></Col>
                                <Col width={50} className={"flex-justify-content-end"}>
                                    <Link href={"/"}>{batchOfGoods?.manufacturer?.name}</Link>
                                </Col>
                            </Row>
                        )}
                        {batchOfGoods?.manufacturer?.address && (
                            <Row>
                                <Col width={50}><b>Manufacturer address:</b></Col>
                                <Col width={50} className={"flex-justify-content-end"}>
                                    <Link href={"/"}>{batchOfGoods?.manufacturer?.address}</Link>
                                </Col>
                            </Row>
                        )}
                        {/*<Row>*/}
                        {/*    <Col width={50}><b>Latest scanned at:</b></Col>*/}
                        {/*    <Col width={50}>14.10.2022</Col>*/}
                        {/*</Row>*/}
                        {batchOfGoods?.createdDate && (
                            <Row>
                                <Col width={50}><b>Production date</b></Col>
                                <Col width={50}>{batchOfGoods.createdDate.toDateString()}</Col>
                            </Row>
                        )}
                        {batchOfGoods?.count && (
                            <Row>
                                <Col width={50}><b>Quantity in the lot</b></Col>
                                <Col width={50}>{batchOfGoods.count}</Col>
                            </Row>
                        )}
                    </CardContent>
                    <CardFooter>
                        <Link iconIos={"material:ios_share"} iconMd={"material:share"}>Share</Link>
                        {/*<Link>Comment</Link>*/}
                    </CardFooter>
                </Card>
            </InnerLayout>
        </Page>
    )
}
