import {Home} from "../screens/Home";
import {Init} from "../screens/Init";
import {Product} from "../screens/Product";
import {LinkParams} from "./LinkParams";
import {f7} from "framework7-react";
import {Scan} from "../screens/Scan";
import {QRScanInfo} from "../components/QRScanner";
import {AirtableClient} from "../services/AirtableClient";
import {GeoLocation} from "../services/GeoLocation";
import {ApiClientInterface} from "../services/ApiClientInterface";

const tabbarLinks = (): LinkParams[] => [
    // {
    //     name: "Home",
    //     props: {
    //         tabLinkActive: f7?.views.main.router.currentRoute.path === "/home",
    //         iconIos: "f7:table_fill",
    //         iconMd: "material:home",
    //         onClick() {
    //             f7.views.main.router.navigate("/home");
    //         }
    //     }
    // },
    {
        name: "Scan QR",
        props: {
            tabLinkActive: f7?.views.main.router.currentRoute.path === "/scan",
            iconIos: "f7:qrcode_viewfinder",
            iconMd: "material:qr_code_scanner",
            onClick() {
                f7.views.main.router.navigate("/scan");
            }
        }
    }
]

export const routes = (dataSource: ApiClientInterface, location: GeoLocation) => {
    return [
        {
            path: "/",
            component: Init,
            options: {
                props: {
                    onInit() {
                        f7.views.main.router.navigate("/home", {
                            transition:"f7-dive"
                        });
                    }
                },
                browserHistory: false,
                reloadAll: false,
            }
        },
        {
            path: "/home",
            component: Home,
            options: {
                props: {
                    screenTitle: "CY Quality Mark",
                    tabbarLinks: tabbarLinks,
                    onScanClick() {
                        f7.views.main.router.navigate("/scan");
                    },
                    onError(err: any) {
                        f7.dialog.alert(err);
                    },
                    dataSource: dataSource
                },
                browserHistory: false,
                reloadAll: false,
            }
        },
        {
            path: "/scan",
            component: Scan,
            options: {
                props: {
                    location: location,
                    screenTitle: "Scan a Product",
                    tabbarLinks: tabbarLinks,
                    onClickBack() {
                        console.log("SCAN BACK");
                        f7.views.main.router.back();
                    },
                    onScan(scanInfo: QRScanInfo) {
                        console.log("SCAN");
                        dataSource.logScan(scanInfo).then((res) => {
                            console.log("logger", res)
                        });
                        f7.views.main.router.navigate(`/product/${scanInfo.batchOfProductId}`, {
                            props: {
                                scanInfo: scanInfo,
                                screenTitle: scanInfo.productName,
                            }
                        })
                    },
                    onError(err: any) {
                        f7.dialog.alert(err);
                    },
                    dataSource: dataSource
                }
            }
        },
        {
            path: "/product/:batchOfProductId",
            component: Product,
            options: {
                props: {
                    tabbarLinks: tabbarLinks,
                    onClickBack() {
                        f7.views.main.router.navigate("/home");
                    },
                    onError(err: any) {
                        f7.dialog.alert(err);
                    },
                    dataSource: dataSource
                }
            }
        }
    ];
}
