import React, {useCallback, useEffect, useMemo, useRef, useState} from "react";
import {Html5Qrcode, Html5QrcodeScannerState} from "html5-qrcode";
import {Html5QrcodeCameraScanConfig, Html5QrcodeConfigs} from "html5-qrcode/esm/html5-qrcode";
import {Html5QrcodeError, Html5QrcodeResult, Html5QrcodeScanType} from "html5-qrcode/esm/core";
import {useWindowSize} from "../utils/useWindowSize";
import QrScanner from 'qr-scanner';
import {GeoLocation} from "../services/GeoLocation";


export interface QRScanInfo {
    batchOfProductId: string,
    productName: string
    manufacturerName: string
    quantityInLot: number
    productionDate: Date
    checksum: string
    location?: GeolocationPosition,
    scannedAt: Date,
}

export interface QRScanError {

}

interface QRPayload {
    id: string
    pn: string
    mn: string
    bs: number
    pd: number
    cs: string
}

export interface QRScannerProps {
    fps?: number

    onLoad(): void

    onScan(scanInfo: QRScanInfo): void

    onError(error: string):void

    location: GeoLocation
}

interface Html5QrcodeScannerConfig extends Html5QrcodeCameraScanConfig, Html5QrcodeConfigs {
    rememberLastUsedCamera?: boolean | undefined;
    supportedScanTypes?: Array<Html5QrcodeScanType> | [];
    showTorchButtonIfSupported?: boolean | undefined;
    showZoomSliderIfSupported?: boolean | undefined;
    defaultZoomValueIfSupported?: number | undefined;
}

export const QRScanner: React.FC<QRScannerProps> = (props: QRScannerProps) => {

    const wSize = useWindowSize();
    const [qrScannerStartPromise, setQrScannerStartPromise] = useState<Promise<any>>();
    const [qrScanner, setQrScanner] = useState<QrScanner>();



    const verify = useCallback((qrPayload: QRPayload) => {
        if (!qrPayload.id || !qrPayload.pn || !qrPayload.bs || !qrPayload.mn || !qrPayload.pd || !qrPayload.cs) {
            throw new Error(`QR code payload is malformed, id, pn, lc, mn, pd, sc is expected, ${qrPayload} given`);
        }
        // @TODO: checksum impl


    }, []);

    const success = useCallback((result: QrScanner.ScanResult) => {
        const qrPayload: QRPayload = JSON.parse(result.data);

        try {
            // verify(qrPayload)
        } catch(err) {
            props.onError(err?.toString() || "Error");
        }

        props.location.getPosition(true).then((location: GeolocationPosition) => {
            props.onScan({
                batchOfProductId: qrPayload.id,
                productName: qrPayload.pn,
                productionDate: new Date(qrPayload.pd),
                manufacturerName: qrPayload.mn,
                quantityInLot: qrPayload.bs,
                checksum: qrPayload.cs,
                location: location,
                scannedAt: new Date(),
            });
        }).catch((err) => {
            props.onScan({
                batchOfProductId: qrPayload.id,
                productName: qrPayload.pn,
                productionDate: new Date(qrPayload.pd),
                manufacturerName: qrPayload.mn,
                quantityInLot: qrPayload.bs,
                checksum: qrPayload.cs,
                scannedAt: new Date(),
            });
            console.log(err);
        });


    }, [props.onScan, verify]);

    const failed = useCallback((errorMessage: string, error?: Html5QrcodeError) => {

    }, [props.onError]);
    const videoRefCallback = useCallback((node: HTMLVideoElement) => {

        console.log("n", node);

        qrScannerStartPromise?.finally(() => {
            qrScanner?.stop();
            qrScanner?.destroy();
        });

        if (node) {
            const qr = new QrScanner(
                node,
                (result: QrScanner.ScanResult) => {
                    console.log(result);
                    success(result);
                },
                {
                    onDecodeError: (error: Error|string) => {
                        console.log(error);
                        failed(error?.toString())
                    },
                    maxScansPerSecond: 30,
                    highlightScanRegion: false,
                    highlightCodeOutline: false,
                }
            );

            const promise = qr.start();
            setQrScannerStartPromise(promise);
            setQrScanner(qr);

            promise.then(() => {
                console.log('QR Scanner started');
                props.onLoad();
            });

            promise.catch((err) => {
                console.log("QR Scanner error", err);
                props.onError(err?.toString());
            });
        }
    }, [failed, success]);

    console.log("RENDER");

    return (
        <video ref={videoRefCallback} style={{width: `${wSize.width}px`}} />
    )
}
